@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

.containar {
    height: 100%;
    display: flex;

}

.right {
    font-family: 'Poppins', sans-serif;
    background-color: rgb(215, 213, 213);
    /* text-align: center; */
    padding: 8px;
    width: 30%;
    height: 100%;

    >li {
        inline-size: none;
    }

}

.name {
    text-align: center;
    font-size: 10px;
    padding-bottom: 10px;

    >img {

        text-align: center;
        border-radius: 50%;
        height: 80px;
        width: 80px;
        text-align: center;

    }

    >p {
        padding: 1px;
    }
}

.spacing {
    letter-spacing: 10px;
}

.Adderss {
    text-align: left;
    padding-left: 5px;

    >p {
        >i {
            position: relative;
            top: 1px;
            bottom: 5px;
            font-size: 15px;
        }
    }
}


.social {
    /* text-align: left; */
    padding: 5px;

}

.profile {
    /* text-align: left; */
    padding: 10px;

}

.Education {
    padding: 10px;
}

.skills {
    >ul {
        columns: 2;
    }
}

.left {
    align-items: right;
    padding: 8px;
    background-color: white;
    text-align: left;
    width: 70%;
    height: 100%;
}



.Education {
    padding-left: 5px;
    padding-top: 10px;
    text-align: left;

    >h5 {
        padding-left: 25px;
    }
}

.edcon {
    display: flex;
}

.edimg {
    padding-right: 1rem;
}

.edbul {
    display: block;
    width: 16px;
    height: 16px;
    background-color: gray;
    border-radius: 50%;
    margin-top: 1.5rem;
}

.edline {
    display: block;
    width: 2px;
    height: 100%;
    background-color: gray;
    transform: translate(7px, 0);
}

.edde {
    display: flex;
    flex-direction: column;
}

.refrences1 {
    padding: 10px;

    >ul {
        columns: 2;
        list-style: none;
        padding-bottom: 20px;
    }
}

.years {
    padding: 20px;
}



.languages {
    padding: 20px;

    >ul {
        columns: 3;
    }
}

.lastline {
    padding-top: 10px;

    >ul {
        columns: 4;
        list-style: none;
    }


    >li {
        margin-right: 10px;

        .i1 {


            padding-left: 5px;
        }

        .i2 {

            padding-left: 7px;
        }

        .i3 {

            padding-left: 8px;
        }

        .i4 {

            padding-left: 12px;
        }
    }
}